import React, { Component } from 'react';

import PostView from "./postView.component";
import * as Constants from "../Constants.js";

export default class ProfilePage extends Component {

    constructor(props) {
        super(props);
        this.state = {isFollowing: false,
            posts: []
            };
    
        // This binding is necessary to make `this` work in the callback
        this.handleFollow = this.handleFollow.bind(this);
        this.OnGoToCommentsButtonClicked=this.OnGoToCommentsButtonClicked.bind(this);
      }

    handleFollow(){
        
        this.setState(state => ({
            isFollowing: !state.isFollowing
          }));
    }
    OnGoToCommentsButtonClicked(postId){

        this.setState({GoToCommentPage:true,
        SelectedPost: postId});
    }


    componentDidMount() {
        console.log("on the profile page")
        //dthis.props.socketHandler("ProfilePage");
        this.getPosts();
      }

    componentWillUnmount(){
        console.log("leaving the profile page")
            
        }
      getPosts(){
        
        fetch(
          Constants.config.url["API_URL"]+"/posts",
            {
              method: "get"
              
            }
          )
            .then((res) => res.json())
            .then((result) => {
                console.log(result);
                this.setState(state => ({
                    posts: result
                  }));
                
              
            })
            .catch((error) => {alert("Error getting Posts", error); alert(error)});

    }





    render() {
       // const elems=["Cat1","Cat2","Cat3","Cat4"];
       // const items=[];
       // for(const [index,value] of elems.entries()){
       //     items.push(<PostView/>);
        //}
        return (
            <div className="Container">

                <div className="Container">
                    <div className="row">
                        <div class="col-1">
                                <img src="data:image/png;base64," style={{width: '60px'}}></img>

                            </div>
                        <div className="col-1">MyUsername</div>

                        <div className="col-1">
                            <button type="button" className="btn btn-primary" onClick={(e)=> this.handleFollow(e)}>{this.state.isFollowing ? 'Following' : 'Follow'}</button>
                        </div>
                    </div>


                </div>

            <div className="Container">
              <div className="row">
                <div className="col-3">
                
                </div>
                <div className="col-6">
                
                
                
                                {this.state.posts.map(item => (
                                     <PostView key={item._id} postId={item._id} postData={item} OnGoToCommentsButtonClicked={this.OnGoToCommentsButtonClicked}/>
                                    ))}



                </div>
                <div className="col-3">
                
                </div>
             </div>
            </div>



            </div>
            
        )
    }
}