import React, { Component } from 'react';
import "./postView.css"
import "./postPage.css"
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faHeart,faComment} from '@fortawesome/fontawesome-free-regular'
import { faHeart as faHeartSolid, faComment as faCommentSolid } from '@fortawesome/free-solid-svg-icons'
import {Redirect, useParams} from 'react-router-dom';
import * as Constants from "../Constants.js";



import { s } from '@fortawesome/free-solid-svg-icons'

export default class PostPage extends Component {

    constructor(props) {
        super(props);
        
        this.state = {isLiked: false, isCommented: false,
        
            postData: [],
                comments: [],
                goBackToHomePage:false,
                commentInputText: '',
                prevDataFromParent: null,
                goToLogin: false
                
                
        };


    
        // This binding is necessary to make `this` work in the callback
        this.handleLike = this.handleLike.bind(this);
        this.handleInputChange2 = this.handleInputChange2.bind(this);
        this.handleComment = this.handleComment.bind(this);
        this.OnPostCommentButtonClicked = this.OnPostCommentButtonClicked.bind(this);
      }

      componentDidMount() {
        console.log("on the Post page")
        
        const { postId } = this.props.match.params
        this.setState(state => ({
            thepostId: postId
          }));
        this.getPost();
      }
      componentWillUnmount(){
        console.log("leaving the Post page")
            
        }

        static getDerivedStateFromProps(props, state) {
            // Any time the current user changes,
            // Reset any parts of state that are tied to that user.
            // In this simple example, that's just the email.
            if (props.dataFromParent !== state.prevDataFromParent) {
                if(props.dataFromParent.id===state.thepostId){
                    if(props.dataFromParent.updateType==="comment"){
                        const {comments} = state;
                        comments.push(props.dataFromParent.comment);
                        console.log("weupdating state");
                        //state.comments.push(props.dataFromParent.comment);
                        return {
                            prevDataFromParent: props.dataFromParent,
                            comments:comments
                          };
                    }
                    else if(props.dataFromParent.updateType==="vote"){
                        const {postData} = state;
                        postData.votes=props.dataFromParent.vote;
                        postData.voters=props.dataFromParent.voters;
                        return {
                            prevDataFromParent: props.dataFromParent,
                            postData: postData
                          };
                    }
                }
             
            }
            return null;
          }


      /*componentDidUpdate(prevProps){
        if (this.props.dataFromParent !== prevProps.dataFromParent) {
            if(this.props.dataFromParent.id===this.state.thepostId){
                if(this.props.dataFromParent.updateType==="comment"){
                    this.state.comments.push(this.props.dataFromParent.comment);
                }
            }
          }
      }*/

      
    handleInputChange2(event) {
        const target = event.target;
        const value = target.value;
        const name = target.name;
    
        this.setState({
          [name]: value
        });
      }

      handleLike(){
        if(!sessionStorage.getItem("username")){
            this.setState({
                goToLogin: true
            })
        }
        else{
            const { postId } = this.props.match.params;
        fetch(
            Constants.config.url["API_URL"]+"/posts/upvote/"+postId,
            {
                credentials: 'include',
               // mode: "same-origin",
              method: "post",
              headers: {
                'Content-Type': 'application/json'}
            }
          )
            .then((res) => res.text())
            .then((result) => {
                console.log("we smashed that like button and  got");
                console.log(result);
                
                
              
            })
            .catch((error) => {alert("Error getting ProfileData", error); alert(error)});
        


         this.setState(state => ({
            isLiked: !state.isLiked
          })); 
        }

        
    }

getPost(){
    const { postId } = this.props.match.params
    fetch(
        Constants.config.url["API_URL"]+"/posts/"+postId,
        {
          method: "get"
          
        }
      )
        .then((res) => res.json())
        .then((result) => {
            console.log(result);
            this.setState(state => ({
                postData: result,
                comments: result.comments
              }));

            
              if(result.voters[sessionStorage.getItem("username")]==="upvote"){
            
                
                this.setState(state => ({
                    isLiked:true
                  }));
            
                }
                else{
            
                    this.setState(state => ({
                        isLiked:false
                      }));
           
                }



              var arr =[postId];
              var x= JSON.stringify(arr);
              this.props.socketHandler(x);
          
        })
        .catch((error) => {alert("Error getting PostData", error); alert(error)});




}
handleComment(){
    this.setState(state => ({
       goBackToHomePage:true
      }));
    }

    OnPostCommentButtonClicked(){
        if(!sessionStorage.getItem("username")){
            this.setState({
                goToLogin: true
            })
        }
        else{
            const { postId } = this.props.match.params
            fetch(
                Constants.config.url["API_URL"]+"/posts/comment/"+postId,
                {
                    credentials: 'include',
                   // mode: "same-origin",
                  method: "post",
                  headers: {
                    'Content-Type': 'application/json'},
                    
                  body: JSON.stringify({
                    
                    "text": this.state.commentInputText
                  }),
                }
              )
                .then((res) => res.text())
                .then((result) => {
                    console.log(result);
                    /* this.setState(state => ({
                        isCommented: !state.isCommented
                      })); */
                    
                  
                })
                .catch((error) => {alert("Error getting ProfileData", error); alert(error)});
        }
        
        }


        
    


    render() {
        if(this.state.goBackToHomePage===true){
            return(
                <Redirect push
                    to={"/"}
                    />
                )
        }
        else if(this.state.goToLogin===true){
            return(
                <Redirect push
                    to={"/login"}
                    />
                )
        }

        //////Assigning default image to jesseerror404 if not found/////////
        var image;
        if(this.state.postData.src != undefined){
            this.image = Constants.config.url["API_URL"]+"/"+this.state.postData.src
        }else{
            this.image = Constants.config.url["API_URL"]+"/images/error404.jpg"
        }
        ////////////////////////////////////////////////////////////        

        return (
            
            <div className="container postContainer">
                <div className="row">
                <div  className="col-7">
                <div className="container">
                <div className="row">
                
                    <div className="col-2">
                        <img src="data:image/png;base64," style={{width: '60px'}}></img>

                    </div>
                    <div className="col-10">
                        {this.state.postData.user}
                    </div>
                    </div>
                <div className="row justify-content-center">
                
                <img className="images" src={this.image} />

                </div>
                <div className="row">
                    <hr size="2px"></hr>
                </div>
                <div className="row bottomHalf">
                    <div className="col-1">
                    <FontAwesomeIcon className="like" icon={this.state.isLiked? faHeartSolid : faHeart} size="2x"  onClick={(e)=> this.handleLike(e)}/>
                    </div>
                    <div className="col-1">
                    <FontAwesomeIcon className="like" icon={faCommentSolid/* this.state.isCommented? faCommentSolid : faComment */} size="2x" onClick={(e)=> this.handleComment(e)}/>
                    </div>
                </div>
                <div className="row">
                <div className="col">
                {this.state.postData.votes}  Likes
                    </div>
                </div>
                <div className="row">
                <div className="col">
                    {this.state.postData.user}: {this.state.postData.text}
                    </div>
                </div>
                </div>
                </div>
                <div  className="col-5">
                <div className="container fill  CommentSection border">
                    <div className="row TopCommentSection border-bottom" >
                            <div className="col d-flex justify-content-center align-items-center">
                            
                            <h2>Comment Section</h2>

                            </div>
                    </div>
                    <div className="row CommentFeed overflow-auto">
                        <div className="col noPad ">
                            
                            <ul className="list-group list-group-flush">
                                    {this.state.comments.map(item => (
                                     <li className="list-group-item text-wrap text-break" key={item._id}>{item.user}: {item.text}</li>
                                    ))}
                                
                               
                            </ul>




                        </div>
                    </div>
                    <div className="row BottomCommentSection border-top" >
                        <div className="col d-flex justify-content-center align-items-center">
                            
                            {/*<div className="d-flex justify-content-center align-items-center align-self-center">*/}
                                <div className="input-group mb-3 noPad noMar">
                                    <input type="text" class="form-control" name="commentInputText" aria-label="Text input with segmented dropdown button for commenting" placeholder="Comment Here"  aria-describedby="button-addon2" value={this.state.commentInputText} onChange={this.handleInputChange2}/>
                                    
                                    <div className="input-group-append">
                                        <button className="btn btn-outline-primary" type="button" id="button-addon2" onClick={(e) => this.OnPostCommentButtonClicked(e)}>Post</button>
                                    </div>
                                </div>
                                {/* </div>*/}

                            </div>
                        
                    </div>
                </div>
                </div>
                </div>
            </div>
        )
    }
}