import React, { Component } from 'react';
import "./postView.css"
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faHeart,faComment} from '@fortawesome/fontawesome-free-regular'
import { faHeart as faHeartSolid, faComment as faCommentSolid } from '@fortawesome/free-solid-svg-icons'

import { s } from '@fortawesome/free-solid-svg-icons'
import {Redirect} from 'react-router-dom';
import * as Constants from "../Constants.js";

export default class PostView extends Component {

    constructor(props) {
        super(props);
        this.state = {isLiked: false, isCommented: false};
    
        if(this.props.postData.voters[sessionStorage.getItem("username")]==="upvote"){           
                this.state.isLiked=true;
        }
        else{
            
                this.state.isLiked=false;
           
        }

        // This binding is necessary to make `this` work in the callback
        this.handleLike = this.handleLike.bind(this);
        this.handleComment = this.handleComment.bind(this);
      }





    handleLike(){
        if(!sessionStorage.getItem("username")){
            this.setState({
                goToLogin: true
            })
        }
        else{
            const { postId } = this.props;
            fetch(
                Constants.config.url["API_URL"]+"/posts/upvote/"+postId,
                {
                    credentials: 'include',
                   // mode: "same-origin",
                  method: "post",
                  headers: {
                    'Content-Type': 'application/json'}
                }
              )
                .then((res) => res.text())
                .then((result) => {
                    console.log("we smashed that like button and  got");
                    console.log(result);
                    
                    
                  
                })
                .catch((error) => {alert("Error getting ProfileData", error); alert(error)});
            
    
    
              this.setState(state => ({
                isLiked: !state.isLiked
              }));  


        }

        
    }
    handleComment(){
        
        this.props.OnGoToCommentsButtonClicked(this.props.postId);
        this.setState(state => ({
            isCommented: !state.isCommented
          }));
        
    }

    componentDidMount() {
        console.log("mounted babby");
        console.log(this.props.postData.voters[sessionStorage.getItem("username")]);
        if(this.props.postData.voters[sessionStorage.getItem("username")]==="upvote"){
            this.setState(state =>({
                isLiked:true
            }));
        }
        else{
            this.setState(state =>({
                isLiked:false
            }));
        }
      }

    componentDidUpdate(prevProps){
        if(this.props.postData!==prevProps.postData){
            if(this.props.postData.voters[sessionStorage.getItem("username")]==="upvote"){
                this.setState(state =>({
                    isLiked:true
                }));
            }
            else{
                this.setState(state =>({
                    isLiked:false
                }));
            }
        }
        

    }



    render() {
        if(this.state.goToLogin===true){
            return(
                <Redirect push
                    to={"/login"}
                    />
                )
        }
        //////Assigning default image to jesseerror404 if not found/////////
        var image;
        if(this.props.postData.src != undefined){
            this.image = Constants.config.url["API_URL"]+"/"+this.props.postData.src
        }else{
            this.image = Constants.config.url["API_URL"]+"/images/error404.jpg"
        }
        ////////////////////////////////////////////////////////////


        return (
            <div className="Container postContainer">
                <div className="row">
                
                    <div className="col-2">
                        <img src="data:image/png;base64," style={{width: '60px'}}></img>

                    </div>
                    <div className="col-10">
                        {this.props.postData.user}
                    </div>
                    </div>
                <div className="row justify-content-center">
                
                <img className="images" src={this.image} />

                </div>
                <div className="row">
                    <hr size="2px"></hr>
                </div>
                <div className="row bottomHalf">
                    <div className="col-1">
                    <FontAwesomeIcon className="like" icon={this.state.isLiked? faHeartSolid : faHeart} size="2x"  onClick={(e)=> this.handleLike(e)}/>
                    </div>
                    <div className="col-1">
                    <FontAwesomeIcon className="like" icon={this.state.isCommented? faCommentSolid : faComment} size="2x" onClick={(e)=> this.handleComment(e)}/>
                    </div>
                </div>
                <div className="row">
                <div className="col pl-4">
                    {this.props.postData.votes} Likes
                    </div>
                </div>
                <div className="row">
                <div className="col pl-4">
                {this.props.postData.user}: {this.props.postData.text}
                    </div>
                </div>

            </div>
        )
    }
}